import { onUpdated, ref, watch, nextTick } from "vue";
export function dimensionsMixin({ containerRef }) {
    const width = ref(0);
    watch(() => containerRef.value?.clientWidth, (nv) => {
        if (nv)
            width.value = nv;
    });
    const updateWidth = () => {
        const initialWidth = containerRef.value?.clientWidth;
        if (initialWidth)
            width.value = initialWidth;
    };
    onUpdated(() => {
        nextTick(() => updateWidth());
    });
    return {
        width
    };
}
